/* Page */
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html,
body,
#root {
  height: 100%;
  width: 100%;
  background-color: #323437;
}

body {
  margin: 0;
  padding: 0;
}

.full-height {
  height: 95vh;
}

.fluid-height {
  height: 65vh;
}

.sf {
  font-family: 'Space Mono', monospace !important;
}

.header-area h1 {
  color: #fff;
  font-family: Fira code, Fira Mono, monospace; 
}

.header-area .header-desc {
  color: #ccc;
}


.dropshadow {
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.25);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.25);
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.25);
}


/* Code Editor Area */
.code-area {
  background-color: #2E2E2E;
  color: #eee;
  height: 100%;
  border-radius: 15px;
  padding: 20px;
  margin: 10px;
}

.code-editor {

}

textarea {
  border: none !important;
  outline: none !important;
}

/* === console area === */
.console-area {
  font-family: Fira code, Fira Mono, monospace; 
  font-size: 13px;

  background-color: #000;
  color: #eee;

  margin: 10px;

  width: 100%;
  height: 100%;

  border-radius: 15px;
}


.tab-container {
  width: 100%;
  height: 40px;
  background-color: #2E2E2E;
  border-radius: 14px 14px 0px 0px;

  padding-left: 8px;
  padding-top: 7px;
}

.tab-container .tab {
  height: 100%;
  background-color: #111;
  color: #eee;
  border: none;
  outline: none;

  padding-left: 15px;
  padding-right: 15px;
  padding-top: 6px;

  font-weight: bold;
  letter-spacing: 1px;

  border-radius: 10px 10px 0px 0px;
}

.active {
  background-color: #000 !important;
}










/* Syntax highlighting */
.token.comment {
  color: #75715E;
}

.token.prolog,
.token.doctype,
.token.cdata {
  color: #90a4ae;
}
.token.punctuation {
  color: #fff;
}
.namespace {
  opacity: 0.7;
}
.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #e91e63;
}
.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #4caf50;
}
.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #eee;
}
.token.atrule,
.token.attr-value,
.token.keyword {
  color: #F92672;
}
.token.function {
  color: #A6E22E;
}
.token.regex,
.token.important,
.token.variable {
  color: #ff9800;
}
.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}
.token.entity {
  cursor: help;
}